import React, { useState, useEffect, useRef } from 'react'
import filterAscGreen from 'assets/icons/Ascending_greenicon.svg'
import filterAscGrey from 'assets/icons/Ascending_greyicon.svg'
import filterCategoryGreen from 'assets/icons/categoryOn.svg'
import filterCategoryGrey from 'assets/icons/categoryOff.svg'
import deleteOn from 'assets/icons/deleteCategoryOn.svg'
import deleteOff from 'assets/icons/deleteCategoryOff.svg'
import checkHover from 'assets/icons/checkHover.svg'
import checkNotHover from 'assets/icons/checkNotHover.svg'
import { useEntryDimention } from 'Page/StockEntry/GeneralServices'
import './index.scss'

const Filter = ({
    isMulti,
    options,
    onChange,
    moduleName
}) => {
    const [show, setShow] = useState(false)
    const { dimention } = useEntryDimention()
    const isMobile = dimention <= 500 ? true : false
    const wrapperRef = useRef(null);

    const leftValue = () => {
        switch (dimention) {
            case 1366:
                return -50
            case 1920:
                return -50
            case 1536:
                return -30
            case 1396:
                return -90
            case 1440:
                return -70
            case 1280:
                return false
            case 1600:
                return -70
            case 1280:
                return false
            case 1024:
                return -180
        }
    }

    const topValue = (moduleName) => {
        switch (dimention) {
            case 1024:
                return -250
            default:
                if (moduleName === 'waste') return -100
                return -220
        }
    }



    //handle auto adjust position

    const elem = document?.getElementById(`c-filter`);
    let rectPosition = elem?.getBoundingClientRect()

    useEffect(() => {
        rectPosition = elem?.getBoundingClientRect()
    }, [show])

    const top = rectPosition?.top + (isMobile ? -70 : topValue(moduleName))

    //handle klick outside

    function useOutside({ wrapperRef, setShow, show }) {
        useEffect(() => {

            function handleClickOutside(event) {
                if (show && wrapperRef.current && !wrapperRef.current.contains(event.target)) {
                    setShow(!show)
                }
            }

            // Bind the event listener
            document.addEventListener("mousedown", handleClickOutside);
            return () => {
                // Unbind the event listener on clean up
                document.removeEventListener("mousedown", handleClickOutside);
            };
        }, [wrapperRef, show]);
    }

    useOutside({ wrapperRef, setShow, show });

    const maxHeight = show ? 280 : 0
    const opacity = show ? 1 : 0

    const left = isMobile ? '1rem' : leftValue()
    const right = isMobile ? '1rem' : false
    const position = isMobile ? 'fixed' : 'absolute'
    const minWidth = isMobile ? '100% !important' : 250

    // data variable
    const [search, setSearch] = useState('')
    const [selected, setSelected] = useState(isMulti ? [] : {})
    const [active, setActive] = useState(false)
    const handleSelected = ({ options, item, setSelected, reset }) => {
        let newOptions = options?.map(data => data)
        newOptions = newOptions.map(data => {
            if (item === data.value) data.selected = !data.selected
            if (reset) data.selected = false
            return data
        })
        newOptions = newOptions.filter(data => data.selected)
        setSelected(newOptions)
        if (typeof (onChange) === 'function') onChange(newOptions)
    }
    const filterBySelected = ((a, b) => {
        if (a.selected && !b.selected) return -1
        else if (!a.selected && b.selected) return 1
        else return 0
    })

    return (
        <div className='position-sticky'>
            <div>

                <img className='cursor-pointer' id='c-filter' src={selected?.length ? filterCategoryGreen : filterCategoryGrey} onClick={() => setShow(true)} />

            </div>
            <div ref={wrapperRef} className='text mt-3 rounded shadow py-2'
                style={{
                    zIndex: 1040,
                    transition: 'max-height ease-in',
                    transitionProperty: 'all',
                    transitionDuration: '0.15s',
                    maxHeight,
                    opacity,
                    minWidth,
                    backgroundColor: 'white',
                    top,
                    position,
                    left,
                    right,
                    overflow: 'hidden',
                    // display: show ? false : 'none'
                }}>
                <div className='px-2'>
                    <div className='border rounded w-100 d-flex justify-content-between align-items-center pl-3 pr-2' style={{ height: 40, }}>
                        <input id='filter-input' value={search} placeholder='Search Category' onChange={e => setSearch(e.target.value)} type='text' className='plain-input m-0 p-0' style={{
                            color: 'rgba(168, 177, 190, 1)'
                            , fontSize: '1rem'
                        }} />
                        <div className='green'>
                            <img
                                src={selected?.length ? deleteOn : deleteOff}
                                alt="delete icon"
                                className="cursor-pointer"
                                onClick={(e) => handleSelected({ options, setSelected, reset: true })}
                            />
                        </div>
                    </div>
                </div>

                <div style={{ maxHeight: 230, overflow: 'scroll' }} className='hide-scrollbar'>
                    {
                        options?.filter(name => {
                            if (search?.length) {
                                if (name.label.toLowerCase().includes(search.toLowerCase())) return name
                            }
                            else return name
                        }).sort(function (a, b) { return filterBySelected(a, b) }).map((data, idx) => {
                            const currentItem = selected.filter(item => item?.value === data?.value)?.length ? true : false
                            const itemOpacity = currentItem ? 1 : 0
                            const itemTextStyle = isMulti ? (currentItem ? (active === idx ? 'green' : 'text') : 'emptyTextField') : (selected ? 'text' : 'emptyTextField')

                            return (
                                <div
                                    onMouseEnter={() => setActive(idx)}
                                    onMouseLeave={() => setActive(null)}
                                    onClick={() => handleSelected({ options, item: data.value, setSelected })}
                                    style={{ minHeight: 40, backgroundColor: 'white' }}
                                    className={`filter-items rounded cursor-pointer w-100 d-flex justify-content-between align-items-center ${itemTextStyle}`}>
                                    <table width={100} style={{ tableLayout: 'fixed' }}>
                                        <tr width={100}>
                                            <td width={10}><img style={{ opacity: itemOpacity }} className='mr-3' src={active === idx && currentItem ? checkHover : checkNotHover} /></td>
                                            <td width={90}>{data.label}</td>
                                        </tr>
                                    </table>
                                </div>
                            )
                        })
                    }
                </div>
            </div>

        </div >
    )
}

export default Filter